@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;400&family=Montserrat:wght@100;300&family=PT+Serif&family=Playfair+Display:ital,wght@1,500&display=swap");
* {
  scroll-behavior: smooth;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-size: 16px;
}

a {
  color: black;
}

body {
  background-color: #f8f8ff;
}
body::-webkit-scrollbar {
  display: none;
}

.miiu-wrapper {
  width: 100vw;
}

.page-slider-enter {
  opacity: 0.01;
}

.page-slider-enter.page-slider-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.page-slider-exit {
  opacity: 1;
}

.page-slider-exit.page-slider-exit-active {
  opacity: 0.01;
}

.transition-group {
  position: relative;
  background-color: #000;
}

@media (min-width: 701px) {
  .miiu-wrapper {
    display: flex;
  }
}/*# sourceMappingURL=app.css.map */